import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ModalController } from '@ionic/angular';
import { never } from 'rxjs';
import { DetailPrimaPage } from './../detail-prima/detail-prima.page'
import {ConfigService} from './../config.service'
import { material } from './../shared/material';

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})

export class Tab1Page {
  public items;
  public selectItem;
  pushPage: any;
  filter: material = new material();
  constructor(private host:ConfigService,private http: HttpClient,private modal: ModalController) { 
      
    this.http.get<any>(this.host.getHost() + '/selectPrima').subscribe(data => {
        this.items = data;
    })
    
  }
  async itemSelected(item: string) {
    const modal = await this.modal.create({
      component: DetailPrimaPage,
      componentProps: {
        items:item
      }
    });
    modal.onDidDismiss().then(()=>{
      this.http.get<any>(this.host.getHost() + '/selectPrima').subscribe(data => {
        this.items = data;
      })
    });
    return await modal.present();
  }
  async addItem() {
    const modal = await this.modal.create({
      component: DetailPrimaPage,
      componentProps: {
        items:null,
      }
    });
    modal.onDidDismiss().then(()=>{
      this.http.get<any>(this.host.getHost() + '/selectPrima').subscribe(data => {
        this.items = data;
      })
    });
    return await modal.present();
  }
}
